import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { CLASS_NAME, SCP } from '../../../../models/dtos/mcg/scp';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'cnpj',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const SCPSearchView: FC<SearchViewProps<SCP>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'cnpj',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'CNPJ',
    },
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];

  const getSearchExpressions = (): SearchExpression[] => {
    return [
      {
        fieldName: 'empresaMCG.id',
        value: globalReducer.globalParameter.empresaMCG.id,
      },
    ];
  };

  return (
    <WindowComponent
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
      globalParameter={globalReducer.globalParameter}
      title="Manutenção de Sociedades em Conta de Participação (SCP)"
      useGlobalParameter={true}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'SCPEditView', CLASS_NAME)}
        searchExpressions={getSearchExpressions}
      />
    </WindowComponent>
  );
};

export default SCPSearchView;
